body {
    font: 100% 'Roboto', Helvetica Neue, Helvetica, Arial, sans-serif;
    margin: 0;
    padding: 0;
    color: #000000;
}

.about {
    margin-left: 30px;
}

.code {
    background: #eeeeee;
}

.menu {
    position: fixed;
    top: 60px;
    left: -240px;
    height: 100%;
    width: 200px;
    background-color: #80b3ff;
    opacity: 0;
    line-height: 2.5em;
    padding: 1em;
    border-right: 2px solid #4277bb;
}

.menu.show {
    left: -20px;
    opacity: 0.97;
    animation-name: enter;
    animation-duration: 0.5s;
}

.menu.hide {
    animation-name: exit;
    animation-duration: 0.5s;
}

.menu ul {
    list-style-type: none;
}

.menu a {
    font-size: 1.4em;
    font-weight: bold;
    text-decoration: none;
    color: white;
}

.menu a:hover {
    color: #f7c245;
}

.content {
    background-color: #fafcff;
}

.content h1 {
    font-size: 3em;
    text-align: center;
    color: #4277bb;
}

.content h3 {
    text-align: center;
    font-weight: 100;
}

.content a {
    cursor: default;
}

.about a, .four-o-four a {
    text-decoration: none;
    color: #5c9ba4;
    cursor: pointer;
}

.about a:hover, .four-o-four a:hover {
    color: #f7c245;
}

.text,
.personal {
    margin: 0px 100px 100px 200px;
    position: absolute;
}

.personal a {
    color: #5c9ba4;
    text-decoration: none;
}

.personal a:hover {
    color: #f7c245;
}

.header {
    position: fixed;
    width: 100%;
    height: 60px;
}

.header button {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    background-image: url('../img/menu.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background-color: transparent;
    border: none;
    width: 40px;
    height: 40px;
    outline: none;
    cursor: pointer;
    outline: none;
}

.header button.selected {
    background-image: url('../img/menu-open.png');
}

.header button::-moz-focus-inner {
    border: 0;
}

div.header h1 {
    position: absolute;
    top: 50%;
    left: 50vw;
    transform: translate(-50%, -50%);
    margin: 0;
    white-space: nowrap;
}

div.container {
    width: 100%;
    margin: 0px;
}

div.header,
div.footer {
    color: #f7c245;
    background: #4277bb;
}

.footer {
    padding-left: 10px;
}

.footer a {
    color: #022639;
}

div.content {
    padding: 5em 1em 1em 1em;
}

.contact {
    text-align: center;
    margin: 25px 0px 0px 400px;
    position: absolute;
}

.inner-flex input.button {
    font-family: Cantarell, Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 1.5em;
    font-weight: bold;
    color: #f7c245;
    background: linear-gradient(#033754, #022639);
    width: 90%;
    padding: 10px;
    margin: 15px;
    cursor: pointer;
    border: 2px #00333d solid;
    border-radius: 4px;
}

.inner-flex input.button:active {
    background: linear-gradient(#011a27, #033754);
}

.inner-flex input.button:focus, .inner-flex a:focus {
    outline: none;
}

.inner-flex input.button::-moz-focus-inner, .inner-flex a::-moz-focus-inner {
    border: 0;
}

.outer-flex {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.inner-flex {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
}

.divider {
    width: 100%;
    text-align: center;
    font-size: 1.5em;
    margin: 20px 0px 2px 0px;
    white-space: nowrap;
}

.divider span {
    background-color: #999;
    color: white;
    border-radius: 5px;
    padding: 5px 10px;
}

@media screen and (min-width: 1280px) {
    .content a {
        width: 25%;
    }
}

@media screen and (min-width: 1020px) and (max-width: 1280px) {
    .content a {
        width: 33.3%;
    }
}

@media screen and (min-width: 700px) and (max-width: 1020px) {
    .content a {
        width: 50%;
    }
}

@media screen and (max-width: 700px) {
    .content a {
        width: 100%;
    }
}

@media screen and (min-width: 1000px) {
    .header h1 {
        font-size: 30px;
    }
}

@media screen and (min-width: 600px) and (max-width: 1000px) {
    .header h1 {
        font-size: 3vw;
    }
}

@media screen and (max-width: 600px) {
    .header h1 {
        display: none;
    }
}

@keyframes enter {
    from {left: -240px; opacity: 0;}
    to {left: -20px; opacity: 0.97;}
}

@keyframes exit {
    from {left: -20px; opacity: 0.97;}
    to {left: -240px; opacity: 0;}
}